<!--收入结算表-->
<template>
    <BoxTitle>
        <div class="month-div">
            <span>月份： </span>
            <el-date-picker v-model="month" type="month" format="yyyy年M月" :picker-options="pickerBeginDateBefore"
                placeholder="选择月" @change="change">
            </el-date-picker>
        </div>
        <div class="item">
            <InfoBox :items="infoIems" :title="monthLabel+'月账单确认总览'"></InfoBox>
        </div>
        <div class="box">
            <div class="print-btn">
                <el-button type="primary" v-print="'#table-content'">打 印</el-button>
                <el-button type="default" @click="html2Excel">导 出</el-button>
            </div>
            <div id="table-content" ref="dom">
                <div class="item">
                    <div class="scorll">
                        <table class="table">
                            <tr class="noBorder head">
                                <td style="text-align:center" colspan="9">先锋绿能与昆仑公司电力加工费结算表</td>
                            </tr>
                            <tr class="noBorder">
                                <td style="text-align:right" colspan="9">金额单位：元</td>
                            </tr>
                            <tr>
                                <th>序号</th>
                                <th>业主单位</th>
                                <th>区域</th>
                                <th>结算月份</th>
                                <th>结算时间</th>
                                <th>销售金额</th>
                                <th>预存电费（45%）</th>
                                <th>结算费用（0.02元/度）</th>
                                <th>先锋结算金额</th>
                            </tr>
                            <template v-for="(item,index) in list">
                                <tr v-for="(tr,m) in item.list">
                                    <td>{{tr.xh}}</td>
                                    <td>{{tr.shortName}}</td>
                                    <td v-if="m==0" :rowspan="item.list.length">{{tr.area}}</td>
                                    <td>{{parseTime(tr.endTime,'{y}年{m}')}}</td>
                                    <td>{{parseTime(tr.startTime,'{y}/{m}/{d}')}}-{{parseTime(tr.endTime,'{y}/{m}/{d}')}}
                                    </td>
                                    <td>{{tr.amount}}</td>
                                    <td>{{tr.prestore}}</td>
                                    <td>{{tr.klFinalAmo}}</td>
                                    <td>{{tr.xfFinalAmo}}</td>
                                </tr>
                            </template>
                            <tr>

                                <td colspan="5">{{parseTime(month,'{y}年{m}')}}合计</td>
                                <td>{{sumAmount}}</td>
                                <td>{{sumPrestore}}</td>
                                <td>{{sumKLAmount}}</td>
                                <td>{{sumXfAmount}}</td>
                            </tr>
                        </table>
                    </div>
                </div>
            </div>
        </div>
    </BoxTitle>
</template>
<script>
    //组件
    import {
        mapState
    } from 'vuex'
    import BoxTitle from "@/components/BoxTitle.vue";
    import InfoBox from "@/components/InfoBox.vue";
    import {
        parseTime,
        getPrevMonth,
        formatNum,
        formatMoneyAddComma
    } from "@/utils/index.js"
    //接口
    import {
        getJsQuery,
        getJsNoConfirm
    } from "@/api/archives.js";
    //公用函数
    import tool from "@/utils/tool.js";
    import Table2Excel from 'table2excel.js'

    export default {
        name: 'useEqManage',
        components: {
            BoxTitle,
            InfoBox
        },
        data() {
            return {
                infoIems: [],
                month: getPrevMonth(26),
                list: [],
                sumAmount: 0,
                sumPrestore: 0,
                sumKLAmount: 0,
                sumXfAmount: 0,
                pickerBeginDateBefore: {
                    disabledDate(time) {
                        return time.getTime() >= (Date.now());
                    }
                },
            }
        },

        computed: {
            ...mapState({
                personName: state => state.user.nickName,
            }),
            date() {
                return parseTime(this.month, '{y}年{m}月27日')
            },
            monthLabel() {

                return parseInt(parseTime(this.month, '{m}'))
            }
        },
        mounted() {
            let style = document.createElement('style')
            style.setAttribute("type", "text/css");
            style.setAttribute("id", "tableStyle");
            style.innerHTML = `
                <!--
                    @page {
                        margin:1.54cm 2cm;               
                    }
                -->
            `;
            this.$refs.dom.appendChild(style);
            this._getData();
        },
        methods: {
            parseTime,
            change() {
                this._getData();
            },
            _getData() {
                this._getNoConfirm(); //查询未确认账单
                getJsQuery({
                    month: parseTime(this.month, '{y}-{m}'),
                }).then(res => {
                    let data = res.rows ? res.rows : [];
                    this.sumAmount = 0;
                    this.sumPrestore = 0;
                    this.sumKLAmount = 0;
                    this.sumXfAmount = 0;
                    //合计
                    //区域合并
                    let arrObj = {};
                    data.map(item => {
                        if (arrObj[item.area]) {
                            arrObj[item.area].push(item)
                        } else {
                            arrObj[item.area] = [item]
                        }
                        this.sumAmount += item.amount;
                        this.sumPrestore += item.prestore;
                        this.sumKLAmount += item.klFinalAmo;
                        this.sumXfAmount += item.xfFinalAmo;
                        item.amount = formatNum(item.amount + '');
                        item.prestore = formatNum(item.prestore);
                        item.klFinalAmo = formatNum(item.klFinalAmo);
                        item.xfFinalAmo = formatNum(item.xfFinalAmo);
                    })
                    this.list = [];
                    let xh = 0;
                    Object.keys(arrObj).forEach(key => {
                        const arr = arrObj[key];
                        arr.forEach(n => {
                            n.xh = ++xh;
                        })
                        this.list.push({
                            area: key,
                            list: arr,
                        })
                    })
                    this.sumAmount = formatMoneyAddComma(this.sumAmount)
                    this.sumPrestore = formatMoneyAddComma(this.sumPrestore);
                    this.sumKLAmount = formatMoneyAddComma(this.sumKLAmount);
                    this.sumXfAmount = formatMoneyAddComma(this.sumXfAmount);
                })
            },
            //查询为确认账单情况
            _getNoConfirm() {
                getJsNoConfirm({
                    month: parseTime(this.month, '{y}-{m}'),
                }).then(res => {
                    this.infoIems = [{
                        label: '当前账单总数',
                        num: res.data.countAll,
                    }, {
                        label: '未确认账单数',
                        num: res.data.count,
                    }, {
                        label: '已确认账单数',
                        num: res.data.countAll - res.data.count,
                    }]
                })

            },
            //下载excel
            html2Excel() {
                new Table2Excel('#table-content table').export(
                    `先锋绿能与昆仑公司电力加工费结算表-${this.parseTime(this.month,'{y}年{m}月')}`);
            },
            //刷新数据
            reloadTable() {
                this._getData();
            }
        },
        activated: function () {
            // 移除任何你不想保留的数据
            this._getData();
        },

    }
</script>
<style scoped lang="scss">
    @media print {
        #table-content {
            zoom: 85%;
            margin-top: 0 !important;
        }

        .head {
            font-size: 18px;
            font-weight: 600;
        }

        .scorll {
            overflow: hidden;
            padding-right: 2px;

        }


        .desc-icon,
        .desc-input {
            display: none !important;
        }

        .desc-print {
            display: inline-block !important;
        }

        .table-h {
            color: #000;
        }

        th,
        td {
            border: .1pt solid !important;
        }

        td,
        th,
        span {
            font-size: 9pt !important;
            padding: 40px 0px !important;
        }

        th,
        td {
            border: .1pt solid !important;
        }

        td,
        span {
            color: #000;
        }

        .table-footer,
        .table-h-list,
        .table-h {
            line-height: 0.3in !important;
        }

        .left {
            margin-left: 3in !important;
        }

        .right {
            margin-right: 2in !important;
        }
    }

    #table-content {
        margin-top: 24px;
    }

    .month-div {
        position: absolute;
        top: 12px;
        right: 24px;
        z-index: 2;

        /deep/ .el-date-editor {
            width: 1.4rem;
        }
    }


    .scorll {
        padding-bottom: 10px;
    }

    .noBorder {
        td {
            border: 0 !important;
        }

        &:hover {
            background-color: transparent !important;
        }
    }

    .box {
        padding-top: 24px;
        position: relative;

        .print-btn {
            position: absolute;
            right: 0;
            top: 0;
        }

        .desc-print {
            display: none;
        }

        .desc-input {
            border: 0;
            outline: none;
            color: #303133;
            font-size: 14px;
            font-family: "PFM" !important;
        }
    }

    th,
    td {
        border: 1px solid #d3d3d3;
    }

    th,
    td {
        text-align: center;
        padding: 10px 15px;
        white-space: nowrap;
    }

    tr:nth-child(even) {
        // background-color: #f2f2f2;
    }

    .table {
        border-collapse: collapse;
        width: 100%;
    }

    .item {
        margin-bottom: 16px;

        .table-h {
            font-size: 16px;
            text-align: center;
            line-height: 40px;

        }

        .table-h-list {
            line-height: 40px;
            display: flex;
            justify-content: space-between;
            font-size: 14px;

            span {
                text-align: left;

                &:last-child {
                    text-align: right;
                }
            }
        }

        .table-footer {
            display: flex;
            justify-content: space-between;
            line-height: 40px;

            .left {
                margin-left: 150px;
            }

            .right {
                margin-right: 100px;
            }
        }
    }
</style>